<template>
    <div class="ToggleSwitch">
        <label>
            <input type="checkbox" v-on:change="toggle" id="checkbox" />
            <div></div>
        </label>
    </div>
</template>

<script>
export default {
    name: "ToggleSwitch",
    prop: "language",
    event: "language-change",
    props: {
        language: String,
    },
    methods: {
        toggle: function() {
            this.$emit("language-change", this.language);
        },
    },
};
</script>

<style scoped lang="scss">
.ToggleSwitch {
    position: absolute;
    top: 10vh;
    right: 3vw;
    height: 40px;
    margin: auto;
    text-align: center;

    label {
        display: inline-block;
        font-size: 30px;
        height: 1em;
        width: 2em;
        background: grey;
        border-radius: 1em;
        cursor: pointer;

        div {
            height: 1em;
            width: 1em;
            border-radius: 1em;
            box-shadow: 0 0.1em 0.3em rgba(0, 0, 0, 0.3);
            -webkit-transition: all 300ms;
            -moz-transition: all 300ms;
            transition: all 300ms;
            background: #fff
                url("data:image/png;base64,");
            background-size: cover;
            background-position-x: 50%;
        }
    }

    input {
        position: absolute;
        opacity: 0;
    }

    input:checked + div {
        -webkit-transform: translate3d(100%, 0, 0);
        -moz-transform: translate3d(100%, 0, 0);
        transform: translate3d(100%, 0, 0);
        background-image: url("data:image/png;base64,");
    }
}
</style>
