<template>
    <div class="FullSectionCard">
        <ColumnContainer class="header">
            <slot name="header"/>
        </ColumnContainer>
        <RowContainer class="card">
            <slot name="card"/>
        </RowContainer>
    </div>
</template>

<script>
    import RowContainer from "../container/RowContainer";
    import ColumnContainer from "../container/ColumnContainer";

    export default {
        name: "FullSectionCard",
        components: {ColumnContainer, RowContainer},
    }
</script>

<style scoped lang="scss">
    @import "~@/assets/scss/Utils";

    .FullSectionCard {
        width: 100%;
        height: fit-content;
        display: grid;
        grid-template-columns: repeat(4, 25%);
        grid-auto-rows: 25vh;
        justify-items: stretch;
        align-items: stretch;
        justify-content: stretch;
        align-content: stretch;

        .header {
            grid-column: 1 / 5;
            grid-row: 1 / 4;
            width: 100%;
            height: 100%;

            @include laptop {
                grid-column: 2 / 8;
            }
        }

        @include phone {
            display: flex;
            flex-direction: column;
        }

        @include tablet {
            display: flex;
            flex-direction: column;
            display: inherit;
        }

        @include laptop {
            grid-template-columns: repeat(8, 12.5vw);
        }

        .card {
            text-align: center;
            background-color: white;
            grid-column: 2 / 4;
            grid-row: 3 / 4;
            margin-top: 12.5vh;
            z-index: 999;
            border-radius: 8px;
            box-shadow: 0 19px 38px rgba(0, 0, 0, 0.30), 0 15px 12px rgba(0, 0, 0, 0.22);

            * {
                text-align: center;
                padding: 16px;
                width: 100%;
                height: 100%;
            }

            @include phone {
                margin-top: 0;
                box-shadow: unset;
                border-radius: initial;
                flex: 0 0 100%;
                width: 100%;
                height: unset;
                padding: unset;
            }

            @include tablet {
                margin-top: 0;
                box-shadow: unset;
                border-radius: initial;
                flex: 0 0 auto;
                height: unset;
                padding: unset;
            }

            @include laptop {
                grid-column: 2 / 8;
            }
        }
    }
</style>
