<template>
    <div class="Section" :style='[this.styleObject]'>
        <slot/>
    </div>
</template>

<script>
    export default {
        name: "Section",
        data: function () {
            return {
                styleObject: {
                    backgroundColor: 'red',
                    height: "200px",
                }
            };
        },
        props: {
            background: {
                type: String,
                required: true
            },
            height: {
                type: Number,
                required: true
            }
        },
        created: function () {
            this.styleObject.backgroundColor = this.background;
            this.styleObject.height = this.height + "px";
        }
    }
</script>

<style scoped lang="scss">
    @import "~@/assets/scss/Utils";

    .Section {
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        width: 100%;
        vertical-align: center;

        img, i {
            height: 96px;
            padding-left: 64px;
        }

        @include phone {
            h2 {
                display: none;
            }
            a {
                img {
                    padding: 0;
                }
            }
        }
    }
</style>
